import React from "react";
import './navigation.css';

export default function Navigation(props) {
    return (
        <nav>
            <ul>
                <li><a className={`navlink ${props.isReady ? 'show-text' : ''}`} href="#app">About</a></li>
                <li><a className={`navlink ${props.isReady ? 'show-text' : ''}`} href="#main-skills-container">Skills</a></li>
                <li><a className={`navlink ${props.isReady ? 'show-text' : ''}`} href="#my-projects-container">Projects</a></li>
                <li><a className={`navlink ${props.isReady ? 'show-text' : ''}`} href="#footer">Contact</a></li>
            </ul>
        </nav>
    )
}