import React from "react";
import './mainTitle.css';

function TopWrapper(props) {
    return (
        <div className="main-title-container">
            <div className={`title-container ${props.isReady ? 'show-main-title' : ''}`}>
                <h1 id="main-title">Self-taught frontend developer from Russia</h1>
                <p id="main-description">I love coding and always strive for perfection</p>
            </div>
        </div>
    )
}

export default TopWrapper;