import React from "react";
import { useState } from "react";
import './myProjectItem.css';

export default function MyProjectItem(props) {
    const [isAnimated, setIsAnimated] = useState(false);

    let timeID = null;
    const touchScreen = 'ontouchstart' in document;

    const onTouchStart = () => {
        if (!touchScreen) return; 
        if (timeID) clearTimeout(timeID);
        setIsAnimated(true);
        timeID = setTimeout(() => {
            setIsAnimated(false);
            clearTimeout(timeID);
        }, 500);
    }

    return <li className={`my-projects-list-item ${props.needTransform ? props.isEven ? 'left-translation' : 'right-translation' : ''}`}>
    <div className="my-projects-list-img-container">
        <img onClick={onTouchStart} src={props.src} className='my-projects-image' alt="project-img" />
        <div className="project-description-main-container">
            <h3 className="my-project-name">{props.name}</h3>
            <div className="project-description-container">
                <p className="my-project-description">{props.description}</p>
                <a href={props.projectUrl} target='_blank' rel="noreferrer" ><button disabled={isAnimated} className="my-project-open-btn">Open</button></a>
            </div>
        </div>
    </div>
</li>
}