import React, { memo, useEffect, useState } from "react";
import './mainScene.css';
import * as THREE from 'three';
import {GLTFLoader} from 'three/examples/jsm/loaders/GLTFLoader';
import {OrbitControls} from 'three/examples/jsm/controls/OrbitControls';
import waving from '../../models/Waving.glb';
import { createRef } from "react";

function MainScene(props) {
    const [dimensions, setDimensions] = useState({width: 700, height: 300});
    const ref = createRef();

    useEffect(() => ref.current !== null ? createMainScene() : () => {});

    function createMainScene() {
      let mixer, clock = new THREE.Clock();
      const canvas = ref.current;

      const PI = Math.PI;

      let width = window.innerWidth;
      let w = width <= 768 ? width : width / 100 * 55;
      const h = window.innerHeight;

      const renderer = new THREE.WebGLRenderer({
        canvas,
        antialias: true,
        alpha: true,
        premultipliedAlpha: false,
      });

      renderer.setSize(w, h);
      renderer.setPixelRatio(window.devicePixelRatio);
      renderer.outputEncoding = THREE.sRGBEncoding;

      const fov = 60;
      const aspect = w / h;
      const near = 0.1;
      const far = 100;

      const camera = new THREE.PerspectiveCamera(fov, aspect, near, far);
      camera.position.set(-2, 6, 22);

      const controls = new OrbitControls(camera, canvas);
      controls.update();

      controls.enableZoom = false;
      controls.enableRotate = false;
      controls.enablePan = false;

      const scene = new THREE.Scene();
      scene.background = new THREE.Color(0x375387);

      function onError() {
        canvas.remove();
        props.setIsReady(true);
      }

      const loader = new GLTFLoader();
      loader.load(waving, onModelLoaded, null, onError);

      let modelWasLoaded;

      function onModelLoaded(GLTF) {
        const root = GLTF.scene;

        root.rotation.set(-PI / 30, -PI / 60, 0);
        root.position.set(1.3, -11, 0);
        root.scale.set(10, 10, 10);

        mixer = new THREE.AnimationMixer( root );

        const animations = GLTF.animations;
        const animation = animations[ 0 ];

        const greeting = mixer.clipAction( animation );
        greeting.play();

        modelWasLoaded = true;
        setRendererSize(width, w, h);

        scene.add(root);

        render();

        props.setIsReady(true);
      }

      {
        const light = new THREE.SpotLight(0xFFFFFF, 1);
        scene.add(new THREE.AmbientLight(0xFFFFFF, .1));
        light.position.set(0, 10, 15);
        light.target.position.set(0, 5, -10);

        scene.add(light);
      }

      function setRendererSize(inWidth, cnvsWidth, height) {
        if (cnvsWidth >= 650 || height > 750) {
          camera.position.set(-1.75, 6, 22);
        }

        if (cnvsWidth <= 650 && cnvsWidth !== inWidth) {
          camera.position.set(-2, 7, 26);
        }

        if (cnvsWidth <= 535 && cnvsWidth !== inWidth) {
          camera.position.set(-2.5, 7.5, 30);
        }

        if (cnvsWidth <= 465 && cnvsWidth !== inWidth) {
          camera.position.set(-3, 8, 34);
        }

        if (height < 750) {
          camera.position.set(-1.45, 6, 19);
        }

        if (height < 600) {
          camera.position.set(-1.15, 6, 15);
        }

        if (height < 500) {
          camera.position.set(-0.85, 6, 12);
        }

        if (height < 400) {
          camera.position.set(-0.55, 6, 9);
        }
      }

      const onWindowResize = () => {
        let width = window.innerWidth;
        let w = width <= 768 ? width : width / 100 * 55;
        let height = window.innerHeight;

        if (modelWasLoaded) {
          setRendererSize(width, w, height);
        }

        renderer.setSize(w, height);
        renderer.setPixelRatio(window.devicePixelRatio);
        camera.aspect = w / height;
        camera.updateProjectionMatrix();
      };

      window.addEventListener('resize', onWindowResize);

      function render() {
        let mixerUpdateDelta = clock.getDelta();

        mixer.update( mixerUpdateDelta );
        renderer.render(scene, camera);
        requestAnimationFrame(render);
      }
    }

    return <canvas ref={ref} className={props.isReady ? 'show-c1' : ''} id="c" width={dimensions.width} height={dimensions.height}>Your browser does not support canvas tag</canvas>
}

export default memo(MainScene);