import React from "react";
import './aboutMe.css';

export default function AboutMe() {
    return <div className="about-me-container">
        <div className="about-me-title-container">
            <h2 id="about-me-title">Hi there! Nice to meet you!</h2>
            <p className="about-me-description">
                Let me introduce myself. My name is Nikolay, I am 23 years old.
                I like to solve difficult tasks, create different layouts, write a lot of JavaScript code and constantly improve my skills. 
                Very often, when developing an app, I ask myself: "If I were a user, would I use this app? How simple and clear is it? Responsive?" 
                That is why when developing, I put myself in the user's place in order to better understand the essence of the problem and how well I solved it, 
                because there is nothing more important than user experience. I like coding and what I do.
            </p>
        </div>
    </div>
}