import React, { memo } from "react";
import { createRef } from "react";
import { useEffect } from "react";
import * as THREE from 'three';
import {FontLoader} from 'three/examples/jsm/loaders/FontLoader';
import {TextGeometry} from 'three/examples/jsm/geometries/TextGeometry';
import './mySkills.css';

function MySkills() {
    const ref = createRef();
    
    useEffect(() => {
      createBackground();
    }, []);

    function createBackground() {
      const canvas = ref.current;
      const PI = Math.PI;

      const w = window.innerWidth;
      const h = window.innerHeight;

      const renderer = new THREE.WebGLRenderer({
        canvas,
        antialias: true,
        alpha: true,
        premultipliedAlpha: false,
      });

      renderer.setSize(w, h);
      renderer.setPixelRatio(window.devicePixelRatio);
      renderer.outputEncoding = THREE.sRGBEncoding;

      const fov = 60;
      const aspect = w / h;
      const near = 0.1;
      const far = 100;

      const camera = new THREE.PerspectiveCamera(fov, aspect, near, far);
      camera.position.set(0, 5, 13);

      const scene = new THREE.Scene();
      scene.rotation.set(0, 0, 0);
      scene.background = new THREE.Color(0x375387);

      const texts = [
          'jQuery', 'React Native', 'Expo', 'React', 'Redux', 'Three.js', 'HTML', 'CSS', 'JavaScript', 
      ];

      const positions = [
          [-8, 3, 0],
          [7, 5, 0],
          [8, 3, 0],
          [-8, 4.5, 0],
          [-9, 5, 0],
          [8, 4.5, 0],
          [-8, 6, 0],
          [10, 5, 0],
          [8, 6, 0],
      ]

      const loader = new FontLoader();

      texts.forEach( (text, idx) => {
          loader.load('https://dl.dropbox.com/s/gqv9jnoid39qvau/gentilis_regular.typeface.json', function onFontLoaded( font ) {
              const geometry = new TextGeometry(text, {
                  font: font,
                  size: 1,
                  height: 0.1,
              });
  
              const textMesh = new THREE.Mesh(geometry, [
                  new THREE.MeshPhongMaterial({color: 0xFFFFFF}),
                  new THREE.MeshPhongMaterial({color: 0x808080}),
              ]);

              textMesh.position.set(...positions[idx]);
              textMesh.rotation.set(0, -PI / 2, 0);
              
              scene.add(textMesh);    
          });
      } );

      {
        scene.add(new THREE.AmbientLight(0xFFFFFF, 1));

      }

      const onWindowResize = () => {
          const w = window.innerWidth;
          const h = window.innerHeight;

      renderer.setSize(w, h);
      renderer.setPixelRatio(window.devicePixelRatio);
      camera.aspect = w / h;
      camera.updateProjectionMatrix();
    };

    window.addEventListener('resize', onWindowResize);
      render();

      function render() {
        renderer.render(scene, camera);
        scene.rotation.y += 0.005;

        requestAnimationFrame(render);
      }
    }

    return <div className="my-skills-main-container" id="main-skills-container">
        <canvas ref={ref} id="c2" width='400' height="300">Your browser does not support canvas tag</canvas>
        <div className="my-skills-info-container">
            <h3 id="my-skills-title">My Skills</h3>
            <table>
            <thead>
              <tr>
                <th>Skills</th>
                <th>Beginner</th>
                <th>Elementary</th>
                <th>Intermediate</th>
                <th>Upper-Intermediate</th>
                <th>Advanced</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>HTML</td>
                <td>⭐</td>
                <td>⭐</td>
                <td>⭐</td>
                <td>⭐</td>
                <td>⭐</td>
              </tr> 
              <tr>
                <td>CSS</td>
                <td>⭐</td>
                <td>⭐</td>
                <td>⭐</td>
                <td>⭐</td>
                <td>⭐</td>
              </tr>
              <tr>
                <td>JavaScript</td>
                <td>⭐</td>
                <td>⭐</td>
                <td>⭐</td>
                <td>⭐</td>
                <td></td>
              </tr>
              <tr>
                <td>React</td>
                <td>⭐</td>
                <td>⭐</td>
                <td>⭐</td>
                <td></td>
                <td></td>
              </tr>
              <tr>
                <td>Redux</td>
                <td>⭐</td>
                <td>⭐</td>
                <td>⭐</td>
                <td></td>
                <td></td>
              </tr>
              <tr>
                <td>Three.js</td>
                <td>⭐</td>
                <td>⭐</td>
                <td>⭐</td>
                <td></td>
                <td></td>
              </tr>
              <tr>
                <td>React Native</td>
                <td>⭐</td>
                <td>⭐</td>
                <td></td>
                <td></td>
                <td></td>
              </tr>
              <tr>
                <td>Expo</td>
                <td>⭐</td>
                <td>⭐</td>
                <td></td>
                <td></td>
                <td></td>
              </tr>
              <tr>
                <td>jQuery</td>
                <td>⭐</td>
                <td>⭐</td>
                <td></td>
                <td></td>
                <td></td>
              </tr>
            </tbody>
            <tfoot></tfoot>
          </table>
        </div>
    </div>
}

export default memo(MySkills);